.login{
    height:100vh;
    width: 100vw;
    background-color: #fff;
    display: grid;
    place-items: center;
}

.login_container{
    padding:100px;
    text-align: center;
    background-color:white;
    border-radius: 10px;
    box-shadow: 0px 1px 3px rgba(0,0,0,0.12),0 1px;
}
.login_container>img{
    object-fit: contain;
    height: 100px;
    margin-bottom: 40px;
}
.login_container >button{
    margin-top: 50px;
    background-color: #0a8d48 !important;
    color:white;
    text-transform: inherit  !important;
}